import React, { useEffect, useState } from 'react'

const App = () => {

    const [articles, setArticles] = useState([]);
    useEffect(() => {
        console.log('use1');
        fetch('http://localhost:3000/articles.json')
        .then(response => response.json())
        .then(data => {
            setArticles(data)
            console.log(data)
            console.log('data')
        });

    }, [])

  return (
    <div>
        <h1>Articles</h1>
        {articles.map(article => {
            <div>
                <h2>{article.title}</h2>
                <p>{article.content}</p>
            </div>
        })}
    </div>
  )
}

export default App